:root {
  --table-height: 575px;
}
body {
  margin: 0;
  background-color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  margin: 0 auto;
  overflow-y: hidden;
}

#map {
  display: flex;
  position: relative;
  height: calc(100vh - var(--table-height));
  width: 100%;

  .icon__container {
    position: absolute;
    right: 10px;
    z-index: 999;
    display: flex;
    flex-direction: column;

    .icon {
      cursor: pointer;
      color: #0d5fba;
    }
  }

  .toggle__button__container {
    position: absolute;
    left: 10px;
    top: 50px;
    z-index: 999;
    display: flex;
    flex-direction: column;

    :not(:last-child) {
      margin-bottom: 10px;
    }

    .icon {
      cursor: pointer;
      width: 50px;
      height: 50px;
    }
  }
}

#tables {
  overflow: auto;
  overflow-y: hidden;

  .table__header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 10px;
    background-color: white;

    .title {
      text-align: left;
      font-size: 20px;
      color: #4d4d4d;
    }

    .table__header__left-container {
      display: flex;
      flex-direction: row;
      align-items: center;
      :first-child {
        margin-right: 10px;
      }

      .download-link {
        display: flex;
        align-items: center;
        padding: 0 5px;
      }
    }
  }

  .MuiTableContainer-root {
    max-height: calc(var(--table-height) - 46px);
    overflow-y: scroll;
  }
}

.e-marker {
  transform-origin: bottom center;
  cursor: pointer;

  &__marker {
    fill: #fff;
  }

  &__icon {
    fill: #fff;
  }
}
